import uniNavBar from '../../components/uni-nav-bar/uni-nav-bar.vue'
import api from '@/api/api.js'
export default {
	data() {
		return {
			userInfo:{}
		};
	},
	globalData:{
		userInfo:null
	},
	onLoad() {
		// this.goLogin()
	},
	components:{
		uniNavBar
	},
	methods:{
		getUserInfo({
			detail
		}){
			this.$log(detail)
			if(detail.userInfo){
				let userInfo = detail.userInfo;
				this.$setStore('userInfo',userInfo)
			}
			
			
		},
		goOrder(state){
			uni.navigateTo({
				url:'../order/order?state='+state
			})
		}
	},
	
}
